import React from 'react';
import shield from '../images/shield.svg';
import thunderbolt from '../images/thunderbolt.svg';

interface Product {
  title: string;
  description: string;
  image: string;
}

const productList: Product[] = [
  {
    title: 'Exorde shield',
    description:
      'A free open-source browser extension that displays a trust score for every information you encounter on the Internet.',
    image: shield,
  },
  {
    title: 'Exorde power',
    description:
      'A free open-source collaborative platform where you do some tasks, and you receive payment in exchange for your efforts.',
    image: thunderbolt,
  },
];

const ProductCard = ({ title, description, image }) => (
  <div className="bg-white p-8 rounded shadow border-4 border-white border-opacity-50 hover:border-orange-light mt-4 md:mt-0 md:mx-4 md:max-w-md cursor-pointer">
    <div className="w-24 mx-auto">
      <img src={image} alt={title} />
    </div>
    <h3 className="text-center text-blue py-6">{title}</h3>
    <p className="text-justify">{description}</p>
  </div>
);

const Products = () => (
  <div className="bg-grey-light p-8 py-20 md:p-20">
    <h2 className="text-center text-blue font-brand pb-4 md:pb-12">
      Brace yourselves little fake news, Exorde is coming.
    </h2>
    <div className="md:flex justify-center">
      {productList.map(product => (
        <ProductCard
          title={product.title}
          description={product.description}
          image={product.image}
        />
      ))}
    </div>
  </div>
);

export default Products;
